import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { ActivitiesComponent } from './activities.component';
import { ActivityCardComponent } from './activities-card.component';
import { ActivitiesGridComponent } from './activities-grid.component';

const routes: Routes = [
  { path: '', component: ActivitiesComponent }
];

@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ]
})
export class ActivitiesRoutingModule {
  static components = [ ActivitiesComponent, ActivityCardComponent, ActivitiesGridComponent ];
}


